<template>
  <div>
    <div class="panel">
      <ljs-tree-table :root-loader="rootLoader" :tree-loader="treeLoader"
                      :border="border" :columns="columns"
                      :driver="driver" :menu-getter="menuGetter"
                      :tree-updater="treeUpdater"
                      :onExpand="onExpand" style="width:100%;height:100%;" :onClose="onClose" :debug="debug"
                      :fixLeft="fixLeft" :fixRight="fixRight"
                      @on-check="onCheck"/>
    </div>
    <input type="button" @click="debug=!debug" :value="debug?'':''"/>
    <input type="button" :value="border?'':''" @click="border=!border"/>
  </div>
</template>
<script>
/* eslint-disable no-proto */

import axios from 'axios'
import LjsTreeTable from '../../lib/ljs-tree-table/ljs-tree-table'
import baseService from '../../utils/baseService.js'
import DataLoader from '../../lib/ljs-tree-table/DataLoader'

const productLineService = baseService('productline.json')
const productLineLoader = new DataLoader((cb, ctx) => productLineService.search().then(response => response.data.ok ? cb.onLoad(response.data.data) : cb.onError(response.data.msg)).catch(cb.onError).then(cb.onEnd))
export default {
  components: {LjsTreeTable},
  data () {
    return {
      rootLoader: productLineLoader,
      treeLoader: productLineLoader,
      treeUpdater: productLineLoader,
      debug: false,
      border: true,
      fixLeft: false,
      fixRight: false,
      // 
      driver: {
        // 
        loader: {
          ProductLine (node, refresh) {
            node['nodes'] = []
            // 
            axios({
              method: 'GET',
              url: window.apiUrl + '/productline.json',
              params: {superId: node.id, onePageShow: 10000}
            })
              .then(function (response) {
                let datas = response.data.data
                if (datas instanceof Array) datas.map(function (val) { node.nodes.push(val) })
                refresh()
              })
              .catch(function (error) {
                console.log(error)
              })
            // 
            axios({
              method: 'GET',
              url: window.apiUrl + '/product.json',
              params: {productLineId: node.id, onePageShow: 10000}
            }).then(function (response) {
              let datas = response.data.data
              if (datas instanceof Array) datas.map(function (val) { node.nodes.push(val) })
              refresh()
            }).catch(function (error) {
              console.log(error)
            })
          },
          Product (node, refresh) {
            node['nodes'] = []
            axios({
              method: 'GET',
              url: window.apiUrl + '/product.json',
              params: {superId: node.id, onePageShow: 10000}
            })
              .then(function (response) {
                let datas = response.data.data
                if (datas instanceof Array) datas.map(function (val) { node.nodes.push(val) })
                refresh()
              })
              .catch(function (error) {
                console.log(error)
              })
          }
        },
        // 
        updater: {
          ProductLine (data, column) {
            let params = {id: data.id}
            params[column.key] = data[column.key]
            axios({
              method: 'put',
              url: window.apiUrl + '/productline.json',
              data: params
            })
              .then(function (response) {
                let msg = response.data.msg
                console.log(msg)
              })
              .catch(function (error) {
                console.log(error)
              })
          },
          Product (data, column) {
            let params = {id: data.id}
            params[column.key] = data[column.key]
            axios({
              method: 'put',
              url: window.apiUrl + '/product.json',
              data: params
            })
              .then(function (response) {
                let msg = response.data.msg
                console.log(msg)
              })
              .catch(function (error) {
                console.log(error)
              })
          }
        },
        // 
        adder: {
          Product (data, cb) {
            axios({
              method: 'POST',
              url: window.apiUrl + '/product.json',
              data: data
            })
              .then(cb)
              .catch(function (error) {
                console.log(error)
              })
          },
          ProductLine (data, cb) {
            axios({
              method: 'POST',
              url: window.apiUrl + '/productline.json',
              data: data
            })
              .then(cb)
              .catch(function (error) {
                console.log(error)
              })
          }
        },
        // 
        deleter: {
          Product (data, cb) {
            axios({
              method: 'delete',
              url: window.apiUrl + '/product.json',
              data: [data.id]
            })
              .then(cb)
              .catch(function (error) {
                console.log(error)
              })
          },
          ProductLine (data, cb) {
            axios({
              method: 'delete',
              url: window.apiUrl + '/productline.json',
              data: [data.id]
            })
              .then(cb)
              .catch(function (error) {
                console.log(error)
              })
          }
        }
      },
      menuGetter (ctx) {
        if (ctx) {
          const node = ctx.node
          switch (node.data.pojo) {
            case 'ProductLine':
              return [
                {
                  label: '',
                  click () {
                    node.load()
                  }
                },
                {
                  label: '',
                  click () {}
                },
                {
                  label: '',
                  click () {
                    node.remove()
                  }
                }
              ]
          }
        } else {
          return [{
            label: '',
            click (context) {}
          }]
        }
      },
      // 
      columns: [
        {
          type: 'expand',
          width: 100
        },
        {
          type: 'selection',
          key: 'id',
          width: 18
        },
        {
          label: '',
          width: 50,
          render (h, ctx) {
            return (<div>{ctx.node.data.kids}</div>)
          }
        },
        {
          key: 'id',
          label: '',
          width: 20
        },
        {
          label: '',
          key: 'name',
          dataType: 'Product|ProductLine',
          width: 100
        },
        {
          edit: false,
          label: '',
          key: 'managerName',
          width: 100
        },
        {
          edit: false,
          dataType: 'Product',
          label: 'ID',
          key: 'id',
          width: 100
        },
        {
          label: '',
          key: 'info',
          width: 100
        },
        {
          label: '',
          key: 'planPublish',
          dataType: 'Product',
          width: 100,
          render (h, ctx) {
            return h('DatePicker')
          }
        },
        {
          label: '',
          key: 'factory',
          dataType: 'Product',
          width: 100
        }
      ],
      onExpand (data, go) {
        go()
      },
      onClose (data, go) {
        go()
      }
    }
  },
  methods: {
    onCheck (selectDatas) {
      console.log('' + selectDatas.length + '', selectDatas)
    },
    loadRootCB (response) {
      if (response.data.ok) {
        this.datas = response.data.data
      }
    }
  }
}
</script>
<style lang="scss">
  @import "../../style/vars.scss";

  .panel {
    //@include debug;
    position: absolute;
    top: 100px;
    left: 100px;
    width: 1000px;
    height: 500px;
  }

  .ivu-date-picker {
    .ivu-date-picker-focused input {
      border: none;
    }

    .ivu-input {
      height: auto;
      border: none;
    }
  }
</style>



