<template>
    <div>
        <LjsExpand :expand="expand" :show="show" @on-expand="onChange"/>
        <div>
            <input v-if="expand" type="button" @click="expand = false" value=""/>
            <input v-else type="button" @click="expand = true" value=""/>
            <input v-if="show" type="button" @click="show = false" value=""/>
            <input v-else type="button" @click="show = true" value=""/>
        </div>
    </div>
</template>

<script>
export default {
  name: 'LjsExpandTest',
  methods: {
    onChange () {
      this.expand = !this.expand
    }
  },
  data () {
    return {expand: true, show: true}
  }
}
</script>

<style scoped>

</style>



