<template>
  <div>
    <LjsTreeTable :border="border" :datas="datas" :columns="columns" :debug="debug"/>
    <div>
      <input type="button" :value="debug?'':''" @click="debug=!debug"/>
      <input type="button" :value="border?'':''" @click="border=!border"/>
    </div>
  </div>
</template>

<script>
import dateformat from 'dateformat'
import LjsTreeTable from '../../lib/ljs-tree-table/ljs-tree-table.vue'

export default {
  name: 'LoadLocalData',
  components: {LjsTreeTable},
  data () {
    return {
      border: true,
      debug: false,
      columns: [
        {
          key: 'id',
          label: '',
          expand: true,
          check: true,
          edit: false
        },
        {
          key: 'name',
          label: '',
          width: 50
        },
        {
          key: 'sex',
          label: '',
          render (h, ctx) {
            let re = h('select', {
              props: {
                transfer: true,
                value: ctx.data[ctx.column.key]
              }
            }, [h('option', {props: {value: ''}}, ''), h('Option', {props: {value: ''}}, '')])
            return re
          }
        },
        {
          key: 'age',
          label: ''
        },
        {
          key: 'birthday',
          label: '',
          render (h, ctx) {
            return h('input', {attrs: {type: 'date', value: ctx.data.birthday}})
          }
        },
        {
          key: 'gard',
          label: ''
        }
      ],
      datas: [],
      dateformat: undefined
    }
  },
  methods: {
    getRandName () { return Math.random().toString(36).substr(2) + Math.random().toString(36).substr(2) + Math.random().toString(36).substr(2) },
    getRandGard () { return parseInt((Math.random() * 8 + 1) + '') + '' },
    getRandAge () { return parseInt((Math.random() * 50 + 20) + '') + '' },
    getRandSex () { return Math.random() > 0.5 ? '' : '' },
    getRandBirthday () {
      let rand = Math.random() * new Date().getTime() / 2000
      let date = Math.random() > 0.5 ? new Date(new Date().getTime() + rand) : new Date(new Date().getTime() - rand)
      let da = dateformat(date, 'yyyy-mm-dd')
      return da
    },
    getRandEnable () {
      return Math.random() > 0.5
    },
    initData () {
      let datas = []
      for (let i = 0; i < 1; i++) {
        let data = {
          id: i,
          name: this.getRandName(),
          gard: this.getRandGard(),
          age: this.getRandAge(),
          sex: this.getRandSex(),
          enable: this.getRandEnable(),
          birthday: this.getRandBirthday(),
          nodes: []
        }
        for (let j = 0; j < 1; j++) {
          let data1 = {
            id: i + '-' + j,
            name: this.getRandName(),
            gard: this.getRandGard(),
            age: this.getRandAge(),
            enable: this.getRandEnable(),
            sex: this.getRandSex(),
            birthday: this.getRandBirthday(),
            nodes: []
          }
          for (let k = 0; k < 1; k++) {
            data1.nodes.push({
              id: i + '-' + j,
              name: this.getRandName(),
              gard: this.getRandGard(),
              enable: this.getRandEnable(),
              age: this.getRandAge(),
              sex: this.getRandSex(),
              birthday: this.getRandBirthday()
            })
          }
          data.nodes.push(data1)
        }
        datas.push(data)
      }
      this.datas = datas
    }
  },
  mounted () {
    this.initData()
  }
}
</script>

<style lang="scss" scoped>
</style>



