<!---->
<template>
  <div>
    <LjsTreeTable ref="treetable" :datas="datas" :columns="columns" style="height:500px;"
                  :right-menu="rightMenu"/>
    <input value="" @click="showChange" type="button"/>
  </div>
</template>
<script>
import LjsTreeTable from '../../lib/ljs-tree-table/ljs-tree-table.vue'

export default {
  name: 'submitAllChange',
  components: {LjsTreeTable},
  methods: {
    showChange () {
      let submitDatas = this.$refs.treetable.getSubmitData()
      console.log(submitDatas)
    }
  },
  data () {
    return {
      datas: [],
      columns: [
        {
          label: '',
          key: 'id',
          expand: true
        },
        {
          label: '',
          key: 'name'
        },
        {
          label: '',
          key: 'info'
        }
      ],
      rightMenu: {
        empty: [
          {
            label: '',
            click (context) {
              context.root({
                pojo: 'ProductLine',
                name: ''
              })
            }
          }],
        ProductLine: [
          {
            label: '',
            click (context) {
              context.brother(context.data,
                {
                  pojo: 'ProductLine',
                  name: ''
                })
            }
          },
          {
            label: '',
            click (context) {
              try {
                context.son(context.data,
                  {
                    pojo: 'ProductLine',
                    name: ''
                  })
              } catch (e) {
                alert(e)
              }
            }
          },
          {
            label: '',
            click (context) {
              context.remove(context.data)
            }
          }
        ]
      }
    }
  }
}
</script>
<style scoped>
</style>



